import React, { useState, useRef } from 'react';

function ChatSection({ showExpertSection, toggleExpertSection }) {
  const [userMessage, setUserMessage] = useState('');
  const [chatHistory, setChatHistory] = useState([]);
  const chatHistoryRef = useRef(null); // Reference to chat-history element

  const handleUserMessageChange = (e) => {
    setUserMessage(e.target.value);
  };

  const handleSendMessage = () => {
    if (userMessage.trim() === '') return;
  
    // Add the user's message to the chat history
    setChatHistory([...chatHistory, { text: userMessage, type: 'user' }]);
  
    // Call the toggleExpertSection function to show the ExpertSection
    toggleExpertSection(true);
  
    // Simulate a response from the chatbot (you can customize this part)
    setTimeout(() => {
      const botResponse = 'This is a mock chatbot response.';
      const botResponseWithAnimation = (
        <span className="typewriter">
          <span className="typewriter-text">{botResponse}</span>
        </span>
      );
      setChatHistory([
        ...chatHistory,
        { text: userMessage, type: 'user' },
        { text: botResponseWithAnimation, type: 'bot' },
      ]);
    }, 1000);
  
    // Clear the user input
    setUserMessage('');
  
    // Reveal the chat-history
    chatHistoryRef.current.style.display = 'block';
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSendMessage();
    }
  };

  return (
<section id="Let's-Chat" className="port">
      <div className="container">
        <div className="row text-center mt-5">
          <h1 className="display-3 fw-bold text-capitalize">Let's Chat</h1>
          <div className="heading-line"></div>
        </div>
      </div>

      <div className="container">
        <div className="row pt-2 pb-2 mt-0 mb-3">
          <div className="col-md-6 border-right">
            <div className="bg-white p-3">
              <h2 className="fw-bold text-capitalize text-center">
                Type in your question below
              </h2>
            </div>
          </div>
          <div className="col-md-6">
            <div className="bg-white p-4 text-start">
              <p className="fw-light">
                We understand that AI might not always give the answer you need, we've been there. That's why we've teamed up with experts from all fields
                to get you the right answer in case you're doubting the AI or need more help.
              </p>
            </div>
          </div>
        </div>
      </div>

      <div className="chat-container chatbox">
        <div className={`chat-history${chatHistory.length > 0 ? ' visible' : ''}`} id="chat-output" ref={chatHistoryRef}>
          {chatHistory.map((message, index) => (
            <div
              key={index}
              className={`chat-bubble ${message.type === 'user' ? 'user' : 'bot'}`}
              id="chat-output"
            >
              {message.text}
            </div>
          ))}
        </div>
        <div className="user-input" id="user-input">
          <input
            type="text"
            placeholder="Type a message..."
            value={userMessage}
            onChange={handleUserMessageChange}
            onKeyPress={handleKeyPress}
          />
       <button onClick={handleSendMessage} aria-label="Send Message">
  <i className="fas fa-paper-plane"></i>
</button>

        </div>
      </div>
    </section>
  );
}

export default ChatSection;
